.ccontainer {
  color:white;
  margin-top: 120px;
  background-color:#19376d;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 100vw;
  padding: 58px 10%;
  gap: 10px;
}

.ctext h2 {
  font-size: 80px;
  font-weight: 700;
  letter-spacing: 4px;
}

.ctext p {
  font-size: 40px;
  font-weight: 400;
  letter-spacing: 2px;
}

.clinks {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  list-style: none;
  gap: 26px;
}

.clink {
  display: flex;
  align-items: center;
  gap: 25px;
}

.clink a {
  color: white;
  text-decoration: none;
  font-size: 30px;
  font-weight: 400;
  letter-spacing: 1.5px;
}

@media screen and (max-width: 830px) {
  .container {
    flex-direction: column;
    gap: 23px;
  }

  .text {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .links {
    align-items: center;
  }
}
