.container
{
    position:relative;
    display:flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 49px;
    z-index:1;
    margin-left:10%;
    margin-right:10%;

}
.content
{
    display:flex;
    flex-direction: column;
    align-items:flex-start;
    color:black;
    z-index:1;
}
.htitle
{
    font-size:72px;
    font-weight:900;
    margin-bottom:33px;
    font-family:Georgia, 'Times New Roman', Times, serif;
    background:linear-gradient(90deg,rgba(255,255,255,1)70%,rgba(255,255,255,0)120%
    );
    -webkit-text-fill-color:transparent;
    -webkit-background-clip:text;
}
.description
{
    font-size:72px;
    font-family:'Times New Roman', Times, serif;
    margin-bottom:52px;

}
.contact-btn
{
    text-decoration: none;
    background-color:black;
    color:white;
    border-radius:100px;
    font-size:30px;
    font-weight:600;
    padding:17px 26px;
    box-shadow:0 0 4px 0 rgba(0,0,0,0.25);
}
.heroimg
{
    width: 50%;
    z-index:1;
    border-radius:50%;
    position:relative;
    animation-name:floating;
    animation-duration:3s;
    animation-iteration-count:infinite;
    animation-timing-function:ease-in-out;

    
}
@keyframes floating
{
    0%
    {
        transform:translate(0,0px)
    }
    50%
    {
        transform:translate(0,10px)
    }
    100%
    {
        transform:translate(0,-0px)
    }
    
}
.topBlur
    {
        position:absolute;
        width:50vw;
        height:50vw;
        min-width:350px;
        top:-128px;
        left:-10vw;
        border-radius:764px;
        background:rgba(25,55,109,0.7);
        filter:blur(100px);
        z-index:0;
    }
.bottomBlur
{
        position:absolute;
        width:70vw;
        height:50vw;
        min-width:350px;
        top:250px;
        right:-25vw;
        border-radius:764px;
        background:rgba(25,55,109,0.7);
        filter:blur(100px);
        z-index:0;
    
}
@media screen and (max-width:830px) {
    .container{
        flex-direction:column-reverse;
    }
    .content
    {
        align-items:center;
    }
}